import './App.css';
import { Container, Row, Dropdown, DropdownButton, ButtonGroup } from "react-bootstrap";
import { Routes, Route, } from "react-router-dom";
import { useState } from 'react';
import MouseCard from './components/MouseCard.js'
import MouseDetail from "./components/MouseDetail.js";


function MouseList() {


  let [mouses, setMouses] = useState([

    {
      id: 'ms001',
      itemName: 'G402 Hyperion Fury ',
      content: ':240-4000dpi\nUSB :1000Hz\n :2000 \n:144g',
      price: '49,900',
      price2: 49900,
      imgPath: "http://192.168.0.63:8898/images/mouse1.jpg"
    },
    {
      id: 'ms002',
      itemName: ' G304   ',
      content: ':200-12000dpi\nUSB :1000Hz\n :1000 \n:99g',
      price: '44,690',
      price2: 44690,
      imgPath: "http://192.168.0.63:8898/images/mouse2.jpg"
    },
    {
      id: 'ms003',
      itemName: 'AG0102   ',
      content: ':1800-12800dpi\n:7000fps\n:9\n:116g',
      price: '39,900',
      price2: 39900,
      imgPath: "http://192.168.0.63:8898/images/mouse3.jpg"
    },
    {
      id: 'ms004',
      itemName: 'G402 Hyperion Fury ',
      content: ':200-4000dpi\nUSB :1000Hz\n :8\n:144g',
      price: '49,900',
      price2: 49900,
      imgPath: "http://192.168.0.63:8898/images/mouse4.jpg"
    },
    {
      id: 'ms005',
      itemName: 'Microsoft   ',
      content: '  :Microsoft 2.4GHz\n :3m\n : 2.4GHz\n : 12',
      price: '49,000',
      price2: 49000,
      imgPath: "http://192.168.0.63:8898/images/mouse5.jpg"
    },
    {
      id: 'ms006',
      itemName: '   ',
      content: ':2.4GHz  \n :5~10m\n : 15\n:113g',
      price: '49,900',
      price2: 49900,
      imgPath: "http://192.168.0.63:8898/images/mouse6.jpg"
    },
    {
      id: 'ms007',
      itemName: '  (M585)',
      content: ':1000dpi\n : 24*\n : 10M\n:101g',
      price: '49,900',
      price2: 49900,
      imgPath: "http://192.168.0.63:8898/images/mouse7.jpg"
    },
    {
      id: 'ms008',
      itemName: 'SPA-MMG1PUB  ',
      content: ':200-10000dpi\n:5000FPS\n :2 \n:126g',
      price: '29,900',
      price2: 29900,
      imgPath: "http://192.168.0.63:8898/images/mouse8.jpg"
    },
    {
      id: 'ms009',
      itemName: 'G102 ',
      content: ':200-8000dpi\nUSB :1000Hz\n\n:85g',
      price: '25,410',
      price2: 25410,
      imgPath: "http://192.168.0.63:8898/images/mouse9.jpg"
    },
  ])


  return (
    <div className="App">
      <Routes>
        <Route
          path="/"
          element={
            <div>

              <div style={{ padding: "10px", position: 'relative', left: '423px' }}>
                {[DropdownButton].map((DropdownType, idx) => (
                  <DropdownType
                    as={ButtonGroup}
                    key={idx}
                    id={`dropdown-button-drop-${idx}`}
                    size="sm"
                    variant="secondary"
                    title=""
                  >
                    <Dropdown.Item eventKey="1" onClick={() => {
                      let temp = [...mouses]
                      temp = temp.sort((a, b) => {
                        return a.price2 - b.price2
                      })
                      setMouses(temp)
                    }}> </Dropdown.Item>
                    <Dropdown.Item eventKey="2" onClick={() => {
                      let temp = [...mouses]
                      temp = temp.sort((a, b) => {
                        return b.price2 - a.price2
                      })
                      setMouses(temp)
                    }}> </Dropdown.Item>
                  </DropdownType>
                ))}
              </div>

              <Container>
                <Row sm={1} md={3}>
                  {mouses.map((data, i) => {
                    return (
                      <MouseCard key={data.id} mouses={mouses} mouse={data} i={i} />
                    );
                  })}
                </Row>
              </Container>

            </div>
          }
        />
        <Route path="/MouseDetail/:id" element={<MouseDetail mouses={mouses} />} />
        <Route path="*" element={<div>  .  .111</div>} />

      </Routes>
    </div>
  );
}

export default MouseList;
