import { Card, ListGroup, Button, Alert } from "react-bootstrap";
import { useParams } from "react-router-dom";
import { useState, useEffect } from 'react';
import WritingItem from "./WritingItem.js";
import "./Writing.css";

function MouseDetail({ mouses }) {
  let { id } = useParams();
  
  let mousesInfo = mouses.find((data, i) => {
    return data.id == id;
  });

  const buttonStyle = {
    color: "white",
    border: "none",
    padding: "5px",
    cursor: "pointer",
    float: "right",
    backgroundColor: "#F1A661",
    marginLeft: "3px"
  };
  let [writingData, setWritingData] = useState([

  ]);

  let [inputValue, setInputValue] = useState("");



  return (
    <div>
      <Card style={{ width: "50%", margin: "0 auto" }}>
        <Card.Img variant="top" src={mousesInfo.imgPath} />
        <Card.Body>
          <Card.Title>{mousesInfo.itemName}</Card.Title>
          <Card.Text></Card.Text>
        </Card.Body>
        <ListGroup className="list-group-flush">
          <ListGroup.Item>{mousesInfo.content}</ListGroup.Item>
          <ListGroup.Item>{mousesInfo.price}</ListGroup.Item>
        </ListGroup>
        <Card.Body>
          <Button variant="primary"></Button>
        </Card.Body>
      </Card>


      <div className="container" style={{margin:'10px'}}>
        <div className="todoHeader">
          <div>
            <h1> </h1>
          </div>
        </div>

        <div className="todoSection" style={{marginTop:'10px'}}>
          <form>
            <input value={inputValue} type="text" name="value" placeholder=" "
              onChange={(event) => {
                setInputValue(event.target.value); //input   text  
              }} />

            <button onClick={(event) => {
              event.preventDefault(); //form   (submit) -> submit 
              //    
              //    
              if (inputValue.trim() === "") { // 
                return; //   
              }
              let addItem = {
                writingId: writingData.length + 1,
                writingItemName: inputValue.trim(),
                finished: false,
              }
              let temp = [...writingData];
              temp.push(addItem);
              setWritingData(temp);
              setInputValue('');
            }}></button>
          </form>

          <br />

          {
            writingData.map((data, i) => {
              return (
                <WritingItem key={data.writingId} dataId={data.writingId} writingData={writingData}
                  data={data} setWritingData={setWritingData} buttonStyle={buttonStyle}></WritingItem>
              );
            })
          }
        </div>
      </div>
    </div>
  );
}

export default MouseDetail;
